.main{
  text-align: center;
  justify-content: center;
  display: flex;
  padding: 90px 0 90px 0;
  background-color: rgb(252, 252, 252);
}
.sub-main{
  display: flex;
  justify-content: center;
  height: 550px;
  width: 35%;
  box-shadow: 11px 12px 13px 12px rgb(207, 207, 207);
  padding-top: 30px;
  border-radius: 60px;
  background-color: #fff;
}
.imgs{
  padding-top: 20px;
  justify-content: center;
  display: flex;
}
.img-container{
  
  border-radius: 150px;
  align-items: center;
  display: flex;
  height: 115px;
  width: 115px;
}
.user , .email{
  height: 100px;
  width: 100px;
  border-radius: 130px;
}
input{
  width: 300px;
  height: 50px;
  border-radius: 60px;
  box-shadow: inset 0px 0px 25px 0px #888;
  border: none;
  outline: none;
  background-color: #fff;
}

.email{
  height: 40px;
  width: 40px;
  position: absolute;
  padding: 14px 0 0 25px;
}
.name{
  padding-left: 70px;
  font-size: 20px;
}
.second-input{
  padding-top: 20px;
}
button{
  width: 380px;
  height: 50px;
  border-radius: 60px;
  background-color: #b103fc;
  color: #fff;
  font-size: 25px;
  border: none;
  cursor: pointer;
}
.button{
  padding-top: 30px;
}
.link{
  font-size: 20px;
  font-weight: 400;
}
a{
  color: blue;
}
